
.mainSignIn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: calc(100vh - 137px);
    font-family: 'Montserrat', Courier, monospace;
    text-transform: uppercase;
}

.mainSignInBody {
    position: relative;
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 20px 40px 20px 20px;
    box-shadow: #ececee 0px 0px 10px;
}

.mainSignInBody::before {
    content: '';
    width: 20px;
    height: 100%;
    background-color: #4d7cff;
    position: absolute;
    right: 0;
    top: 0;
}

.mainLogin {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.mainLoginBody {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    gap: 10px;
}

.mainLoginBodyItem {
    font-size: 1.1rem;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.mainLoginBodyItem small {
    text-align: right;
    font-size: .8rem;
    cursor: pointer;
}

.mainLoginBodyItem small:hover {
    color: #4A74EC;
}

.mainLoginBodyItem input {
    padding: 10px;
    border: solid 1px #f4f4f4;
    border-radius: 5px;
    outline: none;
}

.mainLoginBodyItem input:focus {
    border: solid 1px #4A74EC;
}

.mainLoginBodyItemUtils {
    padding: 10px 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.mainLogin button {
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: #4A74EC;
    color: #fff;
    font-size: 1.1rem;
    cursor: pointer;
}

.mainLogin button:hover {
    background-color: #3a5db2;
}
