
.mainCatalog {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mainCatalogBody {
    width: 100%;
    max-width: 1500px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.mainCatalogGrid {
    position: relative;
    flex-grow: 0;
    display: grid;
    padding: 20px;
    width: 100%;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);
}

.mainCatalogGridItem {
    position: relative;
    padding-top: 100%;
    background-color: #fff;
    cursor: pointer;
}

.mainCatalogGridItem * {
    pointer-events: none;
}

.mainCatalogGridItemDetails {
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.mainCatalogGridItemDetailsTitle {
    font-weight: 600;
    margin-bottom: 5px;
}

.mainCatalogGridItemDetailsCategory {
    font-size: 12px;
    color: #999;
    margin-bottom: 10px;
}

.mainCatalogGridItemDetailsPrice {
    font-size: 1.2rem;
    font-weight: 600;
}

.mainCatalogGridItemDetails h3 {
    font-weight: 300;
    font-size: .95rem;
}

.mainCatalogGridItemImg {
    width: 100%;
    padding-top: 100%;
    top: 0;
    position: absolute;
}

.mainCatalogGridItemImg img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.mainCatalogPagination {
    width: 100%;
    max-width: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 50px;
}

.mainCatalogPaginationItem {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    cursor: pointer;
    background-color: #fff;
    border-radius: 30px;
    font-size: .8rem;
    border: solid 1px #ccc;
}

.mainCatalogPaginationItem:hover {
    border-color: #4A74EC;
}

.mainCatalogPaginationItem.active {
    background-color: #4A74EC;
    color: #fff;
    border-color: #4A74EC;
}

@media screen and (max-width: 775px) {
    .mainCatalogGrid {
        grid-template-columns: repeat(2, 1fr) !important;
    }
}

