
.mainNewsHeader {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #F3F3F3;
    padding: 20px;
    /* margin-bottom: 20px; */
    overflow: hidden;
}

.mainNewsHeaderContent {
    position: relative;
    width: 100%;
    max-width: 900px;
    display: flex;
    overflow: hidden;
}

.mainNewsHeaderContentItem {
    width: 100%;
    display: block;
    flex-shrink: 0;
    display: flex;
    gap: 5px;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease-in-out;
    text-align: center;
}

.mainNewsHeaderContentItem strong {
    font-size: 1.2rem;
    text-transform: uppercase;
}

.mainNewsHeader .arrows {
    font-size: 1.2rem;
    z-index: 1;
}

.mainNewsHeader .arrows:hover {
    cursor: pointer;
}

@media screen and (max-width: 775px) {
    .mainNewsHeader .arrows {
        display: none;
    }
}
