
.mainNavbar {
    display: flex;
    width: 100%;
    height: 75px;
    background-color: #FFF;
    z-index: 1000;
    position: relative;
}

.mainNavbarBody {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.mainNavbarBodyLogo {
    width: 100px;
    height: 100%;
}

.mainNavbarBodyLogo img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    cursor: pointer;
}

.mainNavbarBodyOptions {
    display: flex;
    gap: 40px;
    height: 100%;
}

.mainNavbarBodyOptions span {
    font-size: 1.2rem;
    color: #000;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 10px;
    border-bottom: solid 2px #fff;
}

.mainNavbarBodyOptions span:hover {
    border-bottom: solid 2px #000;
}

.mainNavbarBodySearch {
    display: flex;
    gap: 20px;
    align-items: center;
}

.menuNavbar {
    display: none;
}

.mainNavbarBodySearch svg {
    width: 20px;
    height: 20px;
    color: #000;
    cursor: pointer;
}

.mainNavbarBodySearchInput {
    position: relative;
    background-color: #FAFAFA;
    height: 35px;
    border-radius: 35px;
    display: flex;
    align-items: center;
    padding-left: 7px;
    gap: 15px;
    overflow: hidden;
}

.mainNavbarBodySearch span input {
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 1rem;
    color: #000;
    width: 150px;
}

.cartShop {
    position: relative;
}

.cartShopSize {
    position: absolute;
    bottom: -5px;
    right: -10px;
    color: red;
    font-weight: 900;
    font-size: .8rem;
}

.focused {
    border: solid 1px #4A74EC;
}

.mainCollapseMenu {
    display: none;
}

.mainNavbarBodyUtils {
    display: flex;
    gap: 20px;
    align-items: center;
}

.mainNavbarBodyUtils span {
    cursor: pointer;
}

@media screen and (max-width: 775px) {
    .mainNavbarBodyOptions {
        display: none;
    }

    .cartShop {
        display: none;
    }

    .menuNavbar {
        display: block;
    }

    .mainCollapseMenu.active {
        display: block;
        position: absolute;
        top: 75px;
        background-color: #0000007e;
        width: 100%;
        height: calc(100vh - 75px);
    }

    .mainCollapseMenuOptions {
        display: flex;
        flex-direction: column;
        background-color: #FAFAFA;
        box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    }

    .mainCollapseMenuOptions span {
        font-size: 1.2rem;
        cursor: pointer;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px 20px;
        border-bottom: solid 2px #f4f4f4;
    }

    .mainCollapseMenuOptions span:hover {
        border-bottom: solid 2px #86A2F2;
    }
}
