
.mainHome {
    display: flex;
    flex-direction: column;
    padding: 50px;
    align-items: center;
}

.mainHomeContent {
    width: 100%;
    max-width: 1570px;
    display: flex;
    flex-direction: column;
}

.mainHome h1 {
    font-size: 1.8rem;
    font-weight: 300;
    margin-bottom: 20px;
}

.mainHomeSections {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
}

.mainHomeSection {
    position: relative;
    min-width: 300px;
    height: 300px;
    background-color: #fff;
    display: flex;
    gap: 20px;
    flex: 1;
    padding: 25px;
    flex-direction: column-reverse;
}

.mainHomeSection img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainHomeSection span {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.144);
    background-image: linear-gradient(0deg, rgba(0,0,0) 0%, rgba(0, 0, 0, 0.589) 100%);
}

.mainHomeSectionContent {
    z-index: 1;
    color: #fff;
}

.mainHomeSectionContent p {
    font-size: .85rem;
    font-weight: 300;
    margin-bottom: 20px;
    letter-spacing: .5px;
}

.mainHomeSectionContent button {
    padding: 10px 20px;
    border: none;
    background-color: #fff;
    color: #000;
    font-size: .8rem;
    font-weight: 600;
    border-radius: 20px;
    cursor: pointer;
}
