.mainProfile {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat';
}

.mainProfileBody {
    width: 100%;
    max-width: 1500px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #fff;
}

.mainProfileBodyHeader {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 20px;
    border-bottom: 1px solid #e6e6e6;
    gap: 50px;
    word-break: break-all;
}

.mainProfileBodyHeader svg {
    flex-shrink: 0;
}

.mainProfileBodyHeaderImg {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: #e6e6e6;
    flex-shrink: 0;
}

.mainProfileBodyHeaderImg img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.mainProfileBodyHeaderInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    font-size: 1.2rem;
}

.mainProfileBodyHeaderInfo strong {
    text-transform: uppercase;
}

.mainProfileBodyHeaderInfo > * {
    display: flex;
    gap: 10px;
    align-items: center;
}

.mainProfileBodyOptions ul {
    display: flex;
    list-style: none;
    padding: 0 10px;
}

.mainProfileBodyOptions ul li {
    font-size: 1.2rem;
    padding: 10px 10px;
    height: 100%;
    font-weight: 500;
    cursor: pointer;
    border-bottom: solid 2px #fff;
}

.mainProfileBodyOptions ul li:hover,
.mainProfileBodyOptions ul li.active {
    border-bottom: solid 2px #000;
}

.mainProfileBodyContent {
    width: 100%;
    padding-top: 20px;
}

.mainFavorites .mainCartItemImg {
    width: 100px;
    height: 100px;
}

.mainFavorites .mainCartItem {
    cursor: pointer;
}

.mainFavorites .mainCartBody {
    margin-top: 0;
}

.mainInvoices table {
    width: 100%;
    border-collapse: collapse;
    border: 1px solid #e6e6e6;
}

.mainInvoices table tr {
    text-align: left;
}

.mainInvoices table tr button {
    border: none;
    cursor: pointer;
    font-size: 1rem;
    color: #fff;
    font-weight: 500;
    padding: 5px 10px;
    border-radius: 3px;
    background-color: #4A74EC;
}

.mainInvoices table tr button:hover {
    background-color: #3a5dbb;
}

.mainInvoices table th,
.mainInvoices table td {
    padding: 10px;
}

.mainInvoices table tr:nth-child(2n),
.mainInvoices table th {
    background-color: #f2f2f2;
}

.mainInvoices table tr:hover {
    background-color: #e6e6e6;
}

.mainInvoices table tr td:first-of-type {
    font-weight: 500;
}

