.mainProduct {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat';
}

.mainProductBody {
    width: 100%;
    max-width: 1500px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    background-color: #fff;
}

.mainProductInfosContent {
    padding: 30px;
}

.mainProductInfos, .mainProductInfosContent {
    width: 100%;
    max-width: 430px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: #fff;
    position: relative;
}

.sticky {
    position: fixed;
    top: 0;
    z-index: 1;
}

.mainProductCategory {
    display: flex ;
    align-items: center;
    justify-content: space-between;
}

.mainProductCategory svg {
    width: 20px;
    height: 20px;
    fill: #000;
    cursor: pointer;
}

.mainProductCategory p {
    font-size: 14px;
    font-weight: 300;
    text-transform: uppercase;
}

.mainProductTitle {
    font-size: 2rem;
    font-weight: 500;
    font-style: italic;
    text-transform: uppercase;
}

.mainProductPrice {
    font-weight: 500;
    display: flex;
    gap: 5px;
}

.mainProductPrice s {
    font-weight: 300;
    color: #999;
}

.mainProductPrice p {
    font-weight: 600;
    color: #f00;
}

.mainProductColor {
    font-size: 14px;
    font-weight: 300;
    text-transform: uppercase;
}

.mainProductSize {
    font-size: 0.9rem;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.mainProductSizeOptions {
    display: flex;
    flex-direction: row;
    width: 100%;
    flex-wrap: wrap;
    background-color: #000;
    border: solid 1px #000;
    gap: 1px;
}

.mainProductSizeOptions button {
    width: 100%;
    min-width: 50px;
    flex: 1 0 0;
    height: 50px;
    border: none;
    font-size: 0.9rem;
    font-weight: 500;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #fff;
}

.mainProductSizeOptions button.selected {
    background-color: #000;
    color: #fff;
}

.mainProductSizeOptions button:hover {
    background-color: #000;
    color: #fff;
}

.mainProductBuy {
    width: 100%;
}

.mainProductBuy button {
    width: calc(100% - 4px);
    height: 50px;
    border: none;
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #000;
    color: #fff;
    position: relative;
}

.mainProductBuy button:hover {
    color: rgb(119, 119, 119);
}

.mainProductBuy button::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 3px;
    left: 3px;
    border: solid 1px #000;
}

.mainProductImages {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 40px;
}

.mainProductImage {
    width: 100%;
    min-width: 300px;
    flex: 1 0 0;
}

.mainProductImage:first-of-type {
    min-width: 100%;
}

.mainProductImage img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainProductDetails {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.mainProductDescription {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 10px;
}

.mainProductDescriptionTitle {
    border-top: solid 1px #F3F3F3;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
}

.mainProductDescriptionContent {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding:  0 20px 20px 20px;
    display: none;
}

.mainProductDescriptionContent p {
    font-size: 0.9rem;
    line-height: 1.5;
}

.mainProductDescription.show .mainProductDescriptionContent {
    display: block;
}

.mainProductDescription.show .mainProductDescriptionTitle {
    border-left: solid 4px #000;
}

.mainProductDescriptionContent ul {
    list-style: disc;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-left: 20px;
    gap: 10px;
    font-weight: 400;
}

.mainProductRating {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: flex-start;
}

.mainProductRatingHeader {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.mainProductRatingHeaderTitle {
    display: flex;
    flex: 1;
    flex-direction: row;
    gap: 20px;
    font-size: .9rem;
    text-align: bottom;
    min-width: 197px;
}

.mainProductRatingHeaderTitle span {
    font-weight: 300;
    text-decoration: underline;
    cursor: pointer;
}

.mainProductRatingHeaderStars {
    display: flex;
    flex-direction: column ;
}

.mainProductRatingMainStars {
    width: 100%;
    padding: 10px;
    background-color: #2ADA71;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.mainProductRatingMainStars h2 {
    margin-left: 5px;
}

.mainProductRatingSubStars {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 10px;
}

.mainProductRatingSubStarsItem {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;
}

.mainProductRatingSubStarsItem span {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    font-weight: 300;
}

.mainProductRatingBody {
    display: flex;
    flex: 2;
    flex-direction: column;
    gap: 50px;
}

.mainProductRatingItem {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.mainProductRatingItem strong {
    font-size: .9rem;
    font-weight: 600;
}

.mainProductRatingItem > span {
    font-size: .9rem;
    display: flex;
    gap: 5px;
}

.mainProductRatingItemImages {
    display: flex;
    flex-direction: row;
    gap: 10px;
    width: 100%;
    flex-wrap: wrap;
}

.mainProductRatingItemImages span {
    cursor: pointer;
    width: 90px;
    height: 90px;
}

.mainProductRatingItemImages span img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainProductRatingItem p {
    font-size: .9rem;
    font-weight: 300;
}

.mainProductRatingItemUtils {
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
}

.mainProductRatingItemUtils span {
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: 300;
}

.mainProductRatingItemUtils span svg:hover {
    color: #fff;
    background-color: #000;
    cursor: pointer;
}

.mainProductWriteReviewModal {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mainProductWriteReviewModalContent {
    background: #fff;
    width: 100%;
    max-width: 500px;
    padding: 20px 20px;
    border: solid 1px black;
    position: relative;
}

.mainProductWriteReviewModalContentTitle {
    font-size: 1.1rem;
    font-weight: 600;
    margin-bottom: 20px;
}

.mainProductWriteReviewModalContentStarsTitle {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 10px;
}

.mainProductWriteReviewModalContentStarsTitle input {
    padding: 5px 10px;
    border: solid 1px #000;
    border-radius: 5px;
}

.mainProductWriteReviewModalContentStarsOption {
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin-bottom: 10px;
    padding-top: 10px;
}

.mainProductWriteReviewModalContentStarsOption input {
    display: none;
}

.mainProductWriteReviewModalContentStarsOption svg {
    color: #000;
    cursor: pointer;
    font-size: 2rem;
}

.mainProductWriteReviewModalContent button {
    padding: 10px 20px;
    border: solid 1px #000;
    border-radius: 5px;
    background-color: #000;
    color: #fff;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
}

.mainProductWriteReviewModalContent button:hover {
    background-color: #fff;
    color: #000;
}

.mainProductWriteReviewModalClose {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 2rem;
    cursor: pointer;
}
