
.mainHeaderFilter {
    width: 100%;
    height: 50px;
    background-color: #fff;
    padding: 0px 30px;
    display: flex;
    justify-content: center;
}

.mainHeaderFilterContent {
    max-width: 1200px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    gap: 40px;
}

.mainHeaderFilterContentGrids {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.mainHeaderFilterContentGridsItem {
    display: flex;
    gap: 10px;
    cursor: pointer;
}

.mainHeaderFilterContentGridsItem span {
    content: '';
    width: 20px;
    height: 20px;
    border: solid 1px rgb(190, 190, 190);
    display: inline-block;
    border-radius: 3px;
}

.mainHeaderFilterContentGridsItem.active span {
    border: solid 1px rgb(0, 0, 0);
}

.mainHeaderFilterContentFilter {
    display: flex;
    flex-direction: row;
    gap: 20px;
    cursor: pointer;
}

@media screen and (max-width: 775px) {
    .mainHeaderFilter {
        display: none;
    }
}
