
.mainNotifications {
    position: fixed;
    top: 80px;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
    pointer-events: none;
    padding-right: 5px;
}

.mainNotificationsItem {
    font-family: 'Montserrat', sans-serif;
    width: 300px;
    height: 80px;
    position: relative;
    cursor: pointer;
    pointer-events: all;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    font-weight: 500;
}

.success {
    background-color: #28A745;
}

.error {
    background-color: #DC3545;
    color: #FAFAFA;
}

.mainNotificationsItem::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 7px;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.5);
    animation: resizeTimer 7s linear forwards;
}

.mainNotificationsItem::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 7px;
    background-color: rgba(0, 0, 0, 0.5);
}

@keyframes resizeTimer {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}
