
.mainCheckout {
    width: 100%;
    height: 100%;
    font-family: 'Montserrat', Courier, monospace;
    align-items: center;
    display: flex;
    flex-direction: column;
    background-color: #fff;
}

.mainCheckoutContent {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1500px;
    height: 100%;
    border-top: solid 1px #e6e6e6;
}

.mainCheckoutHeader {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1500px;
    background-color: #fff;
}

.mainCheckoutHeaderItem {
    display: flex;
    flex-direction: row;
    height: 100%;
    padding: 20px;
    align-items: center;
    gap: 10px;
    cursor: pointer;
    border-bottom: solid 1px #fff;
    opacity: .5;
    pointer-events: none;
}

.mainCheckoutHeaderItem * {
    pointer-events: none;
}

.cart .mainCheckoutHeaderItem:nth-child(-n+1),
.delivery .mainCheckoutHeaderItem:nth-child(-n+2),
.payment .mainCheckoutHeaderItem:nth-child(-n+3),
.confirmation .mainCheckoutHeaderItem:nth-child(-n+4) {
    opacity: 1;
    border-color: #000;
}

.mainCheckoutHeaderItem p {
    font-size: .85rem;
    font-weight: 600;
    color: #000;
}

.mainCheckoutHeaderItem span {
    background-color: #000;
    color: #fff;
    width: 23px;
    height: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: .75rem;
    font-weight: 600;
}

