.mainCheckoutDelivery,
.mainCheckoutPayment,
.mainCheckoutConfirmation {
    width: 100%;
    max-width: 800px;
    padding: 20px;
    gap: 20px;
    display: flex;
    flex-direction: column;
}

.mainCheckoutPayment {
    /* max-width: 400px; */
}

.mainCheckoutDelivery h2,
.mainCheckoutPayment h2,
.mainCheckoutConfirmation h2 {
    text-transform: uppercase;
    font-weight: 800;
}

.mainCheckoutDeliveryBody,
.mainCheckoutPaymentBody,
.mainCheckoutConfirmationBody {
    display: flex;
    gap: 20px;
    flex-direction: column;
}

.mainCheckoutDeliveryBodyItem,
.mainCheckoutPaymentBodyItem {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    gap: 20px;
}

.mainCheckoutDeliveryBodyItem input,
.mainCheckoutPaymentBodyItem input {
    width: 100%;
    border: solid 1px gray;
    padding: 20px;
    font-size: .9rem;
}

.mainCheckoutPaymentBodyItem select {
    width: 100%;
    border: solid 1px gray;
    background-color: #fff;
    padding: 20px;
    font-size: .9rem;
}

