.mainCart {
    font-family: 'Montserrat', Courier, monospace;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.mainCartContent {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1500px;
    height: 100%;
    padding: 20px 50px;
    background-color: #fff;
}

.mainCartHeader {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.mainCartHeader h1 {
    font-size: 2rem;
    font-weight: 800;
    text-transform: uppercase;
}

.mainCartHeader p {
    font-size: 1rem;
    letter-spacing: .5px;
}

.mainCartBody {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    gap: 30px;
}

.mainCartBodyItems {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 100%;
}

.mainCartItem {
    display: flex;
    flex-direction: row;
    width: 100%;
    border: solid 1px #000;
    text-transform: uppercase;
    position: relative;
    padding-right: 30px;
}

.mainCartItemImg {
    width: 240px;
    height: 240px;
    flex-shrink: 0;
}

.mainCartItemImg img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.mainCartItemInfo {
    width: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.mainCartItemInfoHeader {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.mainCartItemInfoHeaderPrice {
    font-weight: 500;
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
    justify-content: flex-end;
}

.mainCartItemInfoHeaderPrice s {
    font-weight: 300;
    color: #999;
}

.mainCartItemInfoHeaderPrice p {
    font-weight: 400;
    color: #f00;
}

.mainCartItemInfoFooter {
    position: absolute;
    bottom: 15px;
}

.mainCartItemInfoFooter select {
    width: 100px;
    height: 50px;
    border: solid 1px #000;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 1rem;
    letter-spacing: .5px;
    padding: 0 5px;
    background-color: #fff;
    font-weight: 800;
}

.mainCartItemClose {
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 10px
}

.mainCartBodyMenu {
    width: 360px;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    gap: 30px;
}

.mainCartBodyMenuPayTypes {
    text-transform: uppercase;
    font-size: .75rem;
    letter-spacing: 1px;
    font-weight: 700;
}

.mainCartBodyMenu img {
    width: 100%;
}

.mainCartBodyMenuBuy {
    width: 100%;
}

.mainCartBodyMenuBuy button {
    width: calc(100% - 4px);
    height: 50px;
    border: none;
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #000;
    color: #fff;
    position: relative;
}

.mainCartBodyMenuBuy button:hover {
    color: rgb(119, 119, 119);
}

.mainCartBodyMenuBuy button::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 3px;
    left: 3px;
    border: solid 1px #000;
}

.mainCartBodyMenuResume {
    display: flex;
    flex-direction: column;
    border: solid 1px #000;
    padding: 20px;
    gap: 15px;
}

.mainCartBodyMenuResume h3 {
    text-transform: uppercase;
    letter-spacing: -.5px;
    font-weight: 800;
}

.mainCartBodyMenuResumeItems {
    display: flex;
    flex-direction: column;
    gap: 15px;
    text-transform: uppercase;
    font-size: .85rem;
}

.mainCartBodyMenuResumeItems span {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.cartResumeFinal {
    font-weight: 700;
}
