
.mainNewProduct {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', Courier, monospace;
}

.mainNewProductBody {
    /* width: 100%; */
    max-width: 1500px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 80px 30px;
    gap: 30px;
}

.mainNewProductBodyItems {
    width: 100%;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.mainNewProductBodyItem {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.mainNewProductBodyItem > span {
    font-size: 1.2rem;
    font-weight: 600;
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
    font-family: 'Montserrat', Courier, monospace;
    background-color: #fff;
}

.mainNewProductBodyItem input {
    border: none;
    width: 100%;
}

.mainNewProductBodyItem input:focus {
    outline: none;
}

.mainNewProductBodyItemDouble {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 10px;
}

.tagSystem, .imageSystem {
    font-size: 1.2rem;
    font-weight: 600;
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
    background-color: #fff;
}

.tagSystemList, .imageSystemList {
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
}

.tagSystemListItem, .imageSystemListItem {
    display: inline-block;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin: 5px;
    font-size: 1rem;
    font-family: 'Montserrat', Courier, monospace;
    background-color: #fff;
    gap: 10px;
    display: flex;
}

.tagSystemListItem button, .imageSystemListItem button {
    border: none;
    background-color: #fff;
    font-size: 1rem;
    font-family: 'Montserrat', Courier, monospace;
    cursor: pointer;
}

.mainNewProductBody > button {
    width: calc(100% - 4px);
    height: 50px;
    border: none;
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
    background-color: #000;
    color: #fff;
    position: relative;
}

.mainNewProductBody > button:hover {
    color: rgb(119, 119, 119);
}

.mainNewProductBody > button::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 3px;
    left: 3px;
    border: solid 1px #000;
}
