
.mainInvoice {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1280px;
    background-color: #fff;
    border: solid 1px gray;
}

.mainInvoiceHeader {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    position: relative;
    padding: 20px;
}

.mainInvoiceHeaderLogo {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 150px;
}

.mainInvoiceHeaderLogo img {
    width: 100%;
}

.mainInvoiceHeader h1 {
    text-transform: uppercase;
    font-weight: 700;
}

.mainInvoiceHeaderGrid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    width: 100%;
    font-size: .8rem;
}

.mainInvoiceHeaderGridItem {
    display: flex;
    flex-direction: column;
    gap: 5px;
    min-width: 100%;
}

.mainInvoiceHeaderGridItem strong {
    font-weight: 700;
}

.mainInvoiceAddress {
    padding: 20px;
    background-color: #F8FAFB;
}

.mainInvoiceHeaderGridItemSubGrid {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.mainInvoiceHeaderGridItemSubGridItem {
    display: flex;
    flex-direction: row;
    gap: 5px;
    width: 100%;
    justify-content: space-between;
}

.mainInvoiceProducts {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.mainInvoiceProducts table {
    width: 100%;
    border-collapse: collapse;
}

.mainInvoiceProducts th {
    text-align: left;
    font-weight: 700;
    padding: 5px 0;
}

.mainInvoiceProducts thead {
    border-bottom: solid 1px rgb(196, 196, 196);
    font-size: .8rem;
}

.mainInvoiceProducts tbody {
    font-size: .85rem;
}

.mainInvoiceProducts td {
    padding: 15px 0;
    border-bottom: solid 1px rgb(196, 196, 196);
}

.mainInvoiceFooter {
    background-color: #F8FAFB;
    padding: 0 20px 20px;
    display: flex;
    flex-direction: row;
}

.mainInvoiceFooter > div {
    width: 100%;
}

.mainInvoiceFooterMessage {
    padding: 20px 20px 20px 0;
    display: flex;
    flex-direction: column;
    gap: 5px;
    font-weight: 500;
    color: gray;
}

.mainInvoiceFooterTotal {
    padding: 20px 20px 20px 0;
    display: flex;
    flex-direction: column;
    gap: 5px;
    font-weight: 500;
    color: gray;
}

.mainInvoiceFooterTotalItem {
    display: flex;
    flex-direction: row;
    gap: 5px;
    justify-content: space-between;
}

.mainInvoiceFooterTotalItem:last-of-type {
    border-top: solid 1px gray;
    border-bottom: solid 1px gray;
    font-weight: 700;
    padding: 5px;
    font-size: 1.1rem;
}



