
.mainSideFilter {
    display: flex;
    flex-direction: column;
    width: 300px;
    min-height: 100vh;
    background-color: #fff;
    padding: 0 0 0 20px;
    position: relative;
}

.mainSideFilter::before {
    content: "";
    position: absolute;
    display: block;
    width: 100vw;
    height: 100%;
    right: 100%;
    background-color: #fff;
}

.mainSideFilterItem {
    width: 100%;
    border-bottom: solid 1px rgb(233, 233, 233);
    border-left: none;
    border-right: none;
    overflow: hidden;
}

.mainSideFilterItemTitle {
    width: 100%;
    height: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px 0 0;
    cursor: pointer;
}

.mainSideFilterItemTitle svg {
    width: 20px;
    height: 20px;
    fill: #000;
}

.mainSideFilterItemTitle * {
    pointer-events: none;
}

.mainSideFilterItemBody {
    display: none;
}

.mainSideFilterItemBody.active {
    display: flex;
}

.mainSideFilterItemBody {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 10px 0 20px 0;
}

.mainSideFilterItemBody label {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
    cursor: pointer;
}

@media screen and (max-width: 775px) {
    .mainSideFilter {
        display: none;
    }
}
